import { Injectable } from '@angular/core';
import {
  IManagedObject,
  InventoryService,
  IOperation,
  IResultList,
  OperationService,
  OperationStatus,
  QueriesUtil
} from '@c8y/client';
import {
  DeviceProfile,
  DeviceProfileFirmware,
  DeviceProfileSoftware,
  ComparisonResult
} from './device-profile.model';
import { AlertService } from '@c8y/ngx-components';
import { sortBy, toArray, get } from 'lodash-es';
import { gettext } from '@c8y/ngx-components';

@Injectable()
export class DeviceProfileService {
  readonly dateFrom = new Date(0);
  readonly dateTo = new Date(Date.now() + 86400000); // 1 day in the future
  private queriesUtil: QueriesUtil;

  private NOT_INSTALLED_WARNING = gettext('Not installed on the device');
  private VERSION_MISSMATCH_WARNING = gettext('Version mismatch');
  private SAME_URL_WARNING = gettext(
    'Installed configuration has the same URL but different name or type than the one in the profile'
  );

  constructor(
    private inventoryService: InventoryService,
    private operationService: OperationService,
    private alertService: AlertService
  ) {
    this.queriesUtil = new QueriesUtil();
  }

  createDeviceProfile(deviceProfile: Partial<DeviceProfile>) {
    return this.inventoryService.create(deviceProfile as IManagedObject);
  }

  getDeviceProfilesByDeviceType(deviceType: string): Promise<IResultList<IManagedObject>> {
    const deviceTypeFilter = {
      __or: [{ 'c8y_Filter.type': deviceType }, { __not: { __has: 'c8y_Filter.type' } }]
    };
    return this.getDeviceProfiles(deviceTypeFilter);
  }

  getDeviceProfiles(andQuery?: any): Promise<IResultList<IManagedObject>> {
    let query: object = {
      type: 'c8y_Profile'
    };
    const filter: object = {
      pageSize: 100,
      withTotalPages: true
    };
    query = this.queriesUtil.addAndFilter(query, andQuery || {});
    return this.inventoryService.listQuery(query, filter);
  }

  async getProfileOperation(deviceId: string | number) {
    const filter: object = {
      deviceId,
      fragmentType: 'c8y_DeviceProfile',
      dateFrom: this.dateFrom.toISOString(),
      dateTo: this.dateTo.toISOString(),
      revert: true,
      pageSize: 1
    };

    const operation = (await this.operationService.list(filter)).data[0];
    return operation && operation.status !== OperationStatus.SUCCESSFUL ? operation : undefined;
  }

  async createProfileOperation(device: IManagedObject, deviceProfile: Partial<DeviceProfile>) {
    let operation;
    const operationCfg: IOperation = {
      deviceId: device.id,
      profileId: deviceProfile.id,
      profileName: deviceProfile.name,
      c8y_DeviceProfile: deviceProfile.c8y_DeviceProfile,
      description: `Assign device profile ${deviceProfile.name} to device ${device.name}`
    };
    try {
      const { data } = await this.operationService.create(operationCfg);
      operation = data;
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
    return operation;
  }

  getFirmwareItems(
    device: IManagedObject,
    selectedProfile: Partial<DeviceProfile>
  ): ComparisonResult[] {
    const deviceFirmware = device.c8y_Firmware;
    const profileFirmware = get(selectedProfile, 'c8y_DeviceProfile.firmware');
    const deviceItems = [];
    const profileItems = [];

    if (deviceFirmware) {
      deviceItems.push(deviceFirmware);
    }
    if (profileFirmware) {
      profileItems.push(profileFirmware);
    }
    return this.createProfileComparison(
      deviceItems,
      profileItems,
      'name',
      'version',
      this.getAlert('firmware')
    );
  }

  getSoftwareItems(
    device: IManagedObject,
    selectedProfile: Partial<DeviceProfile>
  ): ComparisonResult[] {
    const deviceSoftware = device.c8y_SoftwareList;
    const profileSoftware = get(selectedProfile, 'c8y_DeviceProfile.software');
    return this.createProfileComparison(
      deviceSoftware,
      profileSoftware,
      'name',
      'version',
      this.getAlert('software')
    );
  }

  getConfigurationItems(
    device: IManagedObject,
    selectedProfile: Partial<DeviceProfile>
  ): ComparisonResult[] {
    const deviceConfiguration = [];
    Object.keys(device).forEach(key => {
      if (key.slice(0, 18) === 'c8y_Configuration_') {
        deviceConfiguration.push(device[key]);
      }
    });
    const profileConfiguration = get(selectedProfile, 'c8y_DeviceProfile.configuration');
    return this.createProfileComparison(
      deviceConfiguration,
      profileConfiguration,
      'url',
      'type',
      this.getAlert('configuration')
    );
  }

  private getAlert(itemType: string): (comparisionResult: ComparisonResult) => string {
    const notInstalled = (comparisionResult: ComparisonResult) => {
      return !comparisionResult.device ? this.NOT_INSTALLED_WARNING : '';
    };

    switch (itemType) {
      case 'firmware':
      case 'software':
        return (comparisionResult: ComparisonResult) => {
          return comparisionResult.device &&
            comparisionResult.profile &&
            comparisionResult.device.itemDetails !== comparisionResult.profile.itemDetails
            ? this.VERSION_MISSMATCH_WARNING
            : notInstalled(comparisionResult);
        };
      case 'configuration':
        return (comparisionResult: ComparisonResult) => {
          return comparisionResult.device &&
            comparisionResult.profile &&
            (comparisionResult.device.itemName !== comparisionResult.profile.itemName ||
              comparisionResult.device.itemDetails !== comparisionResult.profile.itemDetails)
            ? this.SAME_URL_WARNING
            : notInstalled(comparisionResult);
        };
      default:
        return notInstalled;
    }
  }

  private createProfileComparison(
    deviceItems: any[] = [],
    profileItems: Array<DeviceProfileSoftware | DeviceProfileFirmware> = [],
    mergeByProperty: string,
    propertyNameWithDetails: string,
    getAlert: (comparisionResult: ComparisonResult) => string
  ): ComparisonResult[] {
    const comparisonObj = this.createProfileComparisonFromDeviceItems(
      deviceItems,
      mergeByProperty,
      propertyNameWithDetails
    );
    const extendedComparisonObj = this.extendProfileComparisonWithProfileItems(
      comparisonObj,
      profileItems,
      mergeByProperty,
      propertyNameWithDetails,
      getAlert
    );
    return sortBy(toArray(extendedComparisonObj), 'name');
  }

  private createProfileComparisonFromDeviceItems(
    deviceItems: any[],
    mergeByProperty: string,
    propertyNameWithDetails: string
  ): any {
    return deviceItems.reduce(
      (comapritionItem, deviceItem) =>
        Object.assign(comapritionItem, {
          [deviceItem[mergeByProperty]]: {
            device: {
              itemName: deviceItem.name,
              itemDetails: deviceItem[propertyNameWithDetails],
              itemUrl: deviceItem.url
            },
            profile: undefined
          }
        }),
      {}
    );
  }

  private extendProfileComparisonWithProfileItems(
    comparisonObj: object,
    profileItems: Array<DeviceProfileSoftware | DeviceProfileFirmware>,
    mergeByProperty: string,
    propertyNameWithDetails: string,
    getAlert: (comparisionResult: ComparisonResult) => string
  ) {
    profileItems.forEach(profileItem => {
      const comparisionResult: ComparisonResult = {
        profile: {
          itemName: profileItem.name,
          itemDetails: profileItem[propertyNameWithDetails],
          itemUrl: profileItem.url
        },
        device: comparisonObj[profileItem[mergeByProperty]]
          ? comparisonObj[profileItem[mergeByProperty]].device
          : undefined
      };
      comparisionResult.comparisonAlert = getAlert(comparisionResult);
      comparisonObj[profileItem[mergeByProperty]] = comparisionResult;
    });
    return comparisonObj;
  }
}
